@use '../../helpers' as *; 

.cloud {

    
    &__inner {
        margin-top: rem(-500);

        
        @include laptop {
            margin-top: rem(-350);
        }

        @include tablet {
            margin-top: rem(-270);
        }

        @include mobile {
            margin-top: rem(-226);
        }

        @include mobile-s {
            margin-top: rem(-250);
        }

        &--two {
            margin-top: rem(-694);

            @include laptop {
                margin-top: rem(-400);
            }
    
            @include tablet {
                margin-top: rem(-270);
            }
    
            @include mobile {
                margin-top: rem(-226);
            }
    
            @include mobile-s {
                margin-top: rem(-250);
            }
        }
    }


}